.register-form {
  background-color: transparent;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.9);
  width: 80%;
  max-width: 800px;
  box-sizing: border-box;
  margin: 0 auto;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.register-title {
  text-align: center;
  font-size: 50px;
  margin-bottom: 20px;
  color: #ffffff;
}

.input-group {
  margin-bottom: 20px;
}

.input-group label {
  display: block;
  font-size: 14px;
  margin-bottom: 5px;
  color: #ffffff;
}

.input-group input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  outline: none;
  background-color: transparent; /* Ensure all inputs are transparent */
  border: none;
  border-bottom: 1px solid #ccc;
  color: #ffffff;
  transition: border-bottom-color 0.3s ease;
}

.input-group input:focus {
  border-color: #e96666; /* Consistent focus color */
}

.register-button {
  width: 100%;
  padding: 12px;
  background-color: #c22929;
  border: none;
  color: white;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
  transition: background-color 0.3s;
}

.register-button:hover {
  background-color: #b30000; /* Fixed hover color to match other buttons */
}

.login-link-container {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-link-container p {
  color: #ffffff;
  margin-right: 5px;
}

.login-link {
  text-decoration: none;
  color: #c13030;
}

.auth-form-title {
  color: #ffffff;
}